@mixin flex-center{
    display: flex;
    justify-content: center;
    align-items: center;
}

@mixin main-font{
    font-family: $font-txt;
    font-weight: 500;
}

@mixin title-font{
    font-family: $font-txt;
    font-weight: 700;
}

@mixin title{
    font-family: $font-title;
    font-weight: 700;
}





@mixin shadow{
    box-shadow: inset 2px 5px 5px 0px #F6F6F6 ;
    background-color: #EAEAEA;
}

@mixin shadow-bx{
    box-shadow: 0px 4px 15px 0px #AEAEAE ;
    background-color: #F6F6F6;
}

@mixin box-style{
    border: 2px solid #F6F6F6;
    border-radius: 15px;
}

